import {ArticleDB} from "@/db/ArticleDB";
import {Article} from "@/model/Article";

/**
 * CRUD service for Articles.
 */
class ArticleDBService {
    private db = new ArticleDB();

    public async getArticles(): Promise<Article[]> {
        return this.db.articles.toArray();
    }

    public async getLocalArticleCount(): Promise<number> {
        return this.db.articles.count();
    }

    public async searchArticlesByName(searchTerm: string): Promise<Article[]> {
        return this.db.articles
            .filter(article => article.name.toLowerCase().includes(searchTerm.toLowerCase()))
            // .limit(50)
            .toArray();
    }

    public async searchArticlesByDescription(searchTerm: string): Promise<Article[]> {
        return this.db.articles
            .filter(article => article.description.toLowerCase().includes(searchTerm.toLowerCase()))
            // .limit(50)
            .toArray();
    }

    public async searchArticles(searchTerm: string): Promise<Article[]> {
        if (searchTerm.trim().length === 0) {
            return this.db.articles.toArray();
        }

        const searchString = searchTerm.toLowerCase();
        return this.db.articles
            .filter(article => this.matches(article, searchString))
            // .limit(50)
            .toArray();
    }

    public async searchArticlesWithQuery(searchTerm: string): Promise<Article[]> {
        if (searchTerm.trim().length < 3) {
            return this.db.articles.toArray();
        }

        const prefix = searchTerm.substring(0, 3).toLowerCase();

        const searchString = searchTerm.toLowerCase();
        return this.db.articles
            .where("triples")
            .equals(prefix)
            .filter(article => this.matches(article, searchString))
            // .limit(50)
            .toArray();
    }

    private matches(article: Article, searchString: string) {
        return !searchString ||
            article.name.toLowerCase().includes(searchString) ||
            article.description.toLowerCase().includes(searchString);
    }

    /**
     * Add all given objects to the store, replacing existing.
     */
    public async store(articles: Article[]): Promise<number> {
        console.log("ArticleDBService.store store " + articles.length + " articles")
        return this.db.articles.bulkPut(articles)
            .then(() => articles.length);
    }

    public async clear() {
        this.db.articles.clear();
    }
}

export default new ArticleDBService();
